//menulist
.menulist {
	@include relative;
	padding: {
		top: $leading;
		bottom: $leading; }
	text-align: center;
	background: #E6EAF5; }

.menulist-bg {
	@include absolute(top 0 bottom 0);
	width: 100%;
	@extend %clearfix; }

.foods {
	display: block;
	float: left;
	@include isolated-float; }


.menu-set, .menu-pizza, .menu-drinks, .menu-pasta, .menu-sides, .menu-salad {
	@include relative;
	background: white;
	margin-bottom: $leading *2;
	opacity: 0;
	transform: translateY(50%);
	transition: all 1s ease-out;
	&.fade-in {
		opacity: 1;
		transform: translateY(0); } }

.menu-title-style {
	@include bigger-font;
	@include uppercase;
	@include relative;
	font-weight: normal;
	margin-top: $leading *1; }

.menu-list-style {
	padding: $leading *1; }

.dish {
	width: 100%;
	@extend %clearfix;
	.price {
		float: right;
		color: #999; } }

@media #{$breakpoint-desktop} {
	.menu-pizza, .menu-drinks, .menu-pasta, .menu-sides, .menu-set, .menu-salad {
		@include isolated-float; }
	.menu-set {
		@include offset(3,0); }
	.menu-pizza {
		@include offset(1,0); }
	.menu-drinks {
		@include offset(7,3); }
	.menu-pasta {
		@include offset(5,3); }
	.menu-sides {
		@include offset(1,8); }
	.menu-salad {
		@include offset(1,0); }

	.foods {
		&.pizza-piece {
			@include offset(0,8); }
		&.cheese {
			@include offset(1,20); }
		&.egg {
			@include offset(2,0); }
		&.procoli {
			@include offset(3,30); }
		&.shrimp {
			@include offset(4,10); }
		&.sosige {
			@include offset(5,40); }
		&.whole-pizza {
			@include offset(6,9); }
		&.slice {
			@include offset(10,0); }
		&.wine {
			@include offset(8,25); } } }

@media #{$breakpoint-tablet} {
	.menu-pizza, .menu-drinks, .menu-pasta, .menu-sides, .menu-set, .menu-salad {
		@include isolated-float; }
	.menu-set {
		@include offset(2,0); }
	.menu-pizza {
		@include offset(2,0); }
	.menu-drinks {
		@include offset(5,12); }
	.menu-pasta {
		@include offset(2,0); }
	.menu-sides {
		@include offset(2,12); }
	.menu-salad {
		@include offset(5,21); }

	.foods {
		&.pizza-piece {
			@include offset(0,8); }
		&.cheese {
			@include offset(1,40); }
		&.egg {
			@include offset(2,20); }
		&.procoli {
			@include offset(3,30); }
		&.shrimp {
			@include offset(2,60); }
		&.sosige {
			@include offset(5,60); }
		&.whole-pizza {
			@include offset(8,12); }
		&.slice {
			@include offset(10,2); }
		&.wine {
			@include offset(8,45); } } }

@media #{$breakpoint-mobile} {
	.row {
		.menu-pizza, .menu-drinks, .menu-pasta, .menu-sides, .menu-set,.menu-salad {
			width: 90%;
			float: none;
			margin: {
				left: auto;
				right: auto; } } }
	.foods {
		margin: 2rem; } }
